.dataList {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}

.include_dataItem {
    margin: 5px;
    padding: 15px;
    background-color: var(--secondary-color);
    color: var(--primary-color);
    border-radius: 10px;
    box-shadow: 5px 6px 7px var(--light-color);
}

.avoid_dataItem {
    margin: 5px;
    padding: 15px;
    background-color: var(--avoid);
    color: var(--dark-color);
    border-radius: 10px;
    box-shadow: 5px 6px 7px var(--light-color);
}

.searchable {
    margin: 15px;
    padding: 15px;
}